@import url("https://fonts.googleapis.com/css?family=Roboto");

body {
  font-family: "Roboto", sans-serif !important;
}

.App {
  text-align: center;
  color: black;
}

.custom-btn {
  background-color: #6d963e !important;
  border: 0px solid !important;
  color: #ffffff;
  box-shadow: 0 0 2px #000000 !important;
}

.custom-btn:hover {
  background-color: #6d963e !important;
  border: 0px solid !important;
  color: #000000 !important;
  box-shadow: 0 0 2px #000000 !important;
}

.custom-btn-video {
  color: #ffffff !important;
  background-color: #233157 !important;
  width: 100%;
  padding-top: 3%;
}

.custom-btn-video:hover {
  background-color: rgb(56, 77, 129) !important;
}

h1 {
  font-size: min(max(30px, 5vw), 60px);
}

h2 {
  font-size: min(max(20px, 4vw), 30px);
}

p {
  font-size: min(max(20px, 3vw), 30px);
}

.modal-90w {
  width: 90%;
  max-width: none;
}

.modal-contents-80w {
  width: 80%;
  max-width: none;
}

.hero-background-home {
  background-image: url("https://i0.wp.com/post.healthline.com/wp-content/uploads/2021/11/lavendar-1296x728-header.jpg?w=1155&h=1528");
  background-size: cover;
  padding-top: 20%;
  padding-bottom: 3%;
  padding-left: 3%;
  padding-right: 20%;
}

.TourPageCard {
  textdecoration: "none";
  color: "red";
  borderradius: "1em";
  textalign: "left";
  margin: 2%;
}
